import React from "react";
import { Box, Grid } from "@chakra-ui/react";
import "../../styles/scss/main.scss";
import "../../styles/scss/cards.scss";
import { useState } from "react";
import { useEffect } from "react";
import styled from "styled-components";
import Slider from "./Slider";

//estilos locales del slider
const Title = styled.h1`
  color: #eeeeee;
  font-size: 3rem;
  font-family: "Open Sans";
  width: 100%;
  text-align: center;
`;

const Caracteristicas = styled.div``

export default function Home() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    getData();
  }, []);

     //Traer la data desde la API
     const getData = async () => {
      try {
        const resp = await fetch(
          "https://encasa.parqueexplora.org/wp-json/wp/v2/posts"
        );
        const inData = await resp.json();

        //Guarda la data relevante en el state data
        inData.forEach((d) => {
          const newData = {
            id: d.id,
            title: d.title?.rendered,
            summary: d.excerpt?.rendered,
            url: d.guid?.rendered,
            img: d.jetpack_featured_media_url,
            catagory: d.categories,
          };
          setData((data) => [...data, newData]);
        });

        //Establece que ya se terminó de cargar la data
        setLoading(false);
      } catch {
        console.warn("No se recibió la data");
      }
    };

  return (
    <div style={{ backgroundColor: "#a569d1", boxSizing: "border-box" }}>
      {/* --------------------- Sección 1 ---------------- */}
      <div className="container-fluid p-0">
        <Grid
          display="inline-flex"
          flexDirection="column"
          minHeight="100%"
          justifyContent="space-evenly"
          w="100%"
        >
          {loading ? (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                height: "calc(100vh - 60px)",
              }}
            >
              <div
                className="spinner-border"
                style={{ width: "4rem", height: "4rem", color: "white" }}
                role="status"
              />
            </div>
          ) : (
            <div className="animate__animated animate__bounceInDown">
              <div className="separator" />
              <Box className="content bg-1">
               <Slider data={data}/>
                <Caracteristicas></Caracteristicas>
              </Box>
              <div className="separator" />
              <Title>Novedades</Title>
              <Box
                fontWeight="900"
                textAlign="center"
                width="100%"
                color="white"
                style={{ fontFamily: "Exo2", width: "100%" }}
                className=""
                display="flex"
                alignItems="center"
                justifyContent="space-evenly"
                flexWrap="wrap"
              >
                {data?.map(
                  ({ title, url, img, summary, id }, i) =>
                    i < 3 && (
                      <a href={url} target="_blank" rel="noreferrer" key={id}>
                        <span>
                          <div>
                            <div className="card">
                              <figure>
                                <img src={img} alt={title} />
                              </figure>
                              <div>
                                <h1>{title}</h1>
                                <p>{summary?.replace(/<[^>]+>/g, "")}</p>
                              </div>
                              Ver mas
                            </div>
                          </div>
                        </span>
                      </a>
                    )
                )}
              </Box>
              <div className="separator" />
            </div>
          )}
        </Grid>
      </div>
    </div>
  );
}
